import { useState, useEffect } from 'react';

/**
 * Hook
 * @returns {Object} 
 */
export const useAdminAuth = () => {
  const [isAuthorized, setIsAuthorized] = useState(false);
  const [authData, setAuthData] = useState(null);
  const [loading, setLoading] = useState(true);

  // 
  const validateAdminAccess = () => {
    try {
      // URL
      const authDataStr = localStorage.getItem('admin_auth_data');
      if (authDataStr) {
        const authData = JSON.parse(authDataStr);

        // 24
        const loginTime = new Date(authData.loginTime);
        const now = new Date();
        const hoursDiff = (now - loginTime) / (1000 * 60 * 60);

        if (authData.isAuthenticated && hoursDiff < 24) {
          setIsAuthorized(true);
          setAuthData(authData);
          return true;
        } else {
          // 
          localStorage.removeItem('admin_auth_data');
          localStorage.removeItem('admin_super_token');
          localStorage.removeItem('admin_token_timestamp');
        }
      }

      // URL
      return false;
    } catch (error) {
      console.error('Admin validation error:', error);
      return false;
    }
  };

  // 
  const handleLoginSuccess = authData => {
    setAuthData(authData);
    setIsAuthorized(true);
  };

  // 
  const handleLogout = () => {
    // 
    localStorage.removeItem('admin_auth_data');
    localStorage.removeItem('admin_super_token');
    localStorage.removeItem('admin_token_timestamp');

    // 
    setIsAuthorized(false);
    setAuthData(null);

    // 
    console.log(
      `[Admin Logout]  - ${new Date().toLocaleString()}`
    );
  };

  // 
  const activateAdminMode = () => {
    console.warn('[Admin] ');
    alert('');
  };

  // 
  useEffect(() => {
    const isValid = validateAdminAccess();
    if (isValid) {
      // 
    }
    setLoading(false);

    // 
    window.activateAdminMode = activateAdminMode;

    // 
    const sessionCheckInterval = setInterval(() => {
      const authDataStr = localStorage.getItem('admin_auth_data');
      if (authDataStr) {
        try {
          const authData = JSON.parse(authDataStr);
          const loginTime = new Date(authData.loginTime);
          const now = new Date();
          const hoursDiff = (now - loginTime) / (1000 * 60 * 60);

          if (hoursDiff >= 24) {
            // 
            handleLogout();
            alert('');
          }
        } catch (error) {
          console.error('Session check error:', error);
        }
      }
    }, 60 * 60 * 1000); // 

    // 
    return () => clearInterval(sessionCheckInterval);
  }, []);

  return {
    isAuthorized,
    authData,
    loading,
    handleLoginSuccess,
    handleLogout,
    activateAdminMode,
  };
};
