// tag-parser v1: 

/**
 * 
 * @param {string} text - 
 * @returns {Array<{name: string, slug: string}>} 
 */
export const extractTags = text => {
  if (!text || typeof text !== 'string') {
    return [];
  }

  //  #  1-50
  const tagRegex = /#([a-zA-Z0-9_-]{1,50})/g;
  const matches = text.match(tagRegex);

  if (!matches) {
    return [];
  }

  // 
  const uniqueTags = new Set();
  const tags = [];

  matches.forEach(match => {
    const name = match.slice(1); //  # 
    const slug = name.toLowerCase().trim();

    // 
    if (isValidTag(slug) && !uniqueTags.has(slug)) {
      uniqueTags.add(slug);
      tags.push({ name, slug });
    }
  });

  return tags;
};

/**
 * 
 * @param {string} slug -  slug
 * @returns {boolean} 
 */
const isValidTag = slug => {
  // 
  const blacklist = [
    'the',
    'and',
    'or',
    'but',
    'in',
    'on',
    'at',
    'to',
    'for',
    'of',
    'with',
    'by',
    'from',
    'up',
    'about',
    'into',
    'through',
    'during',
    'before',
    'after',
    'above',
    'below',
    'between',
    'among',
    'within',
    'without',
    'this',
    'that',
    'these',
    'those',
    'is',
    'are',
    'was',
    'were',
    'be',
    'been',
    'being',
    'have',
    'has',
    'had',
    'do',
    'does',
    'did',
    'will',
    'would',
    'could',
    'should',
    'may',
    'might',
    'can',
    'must',
    'shall',
  ];

  return slug.length >= 2 && !blacklist.includes(slug);
};

/**
 * 
 * @param {Array<{name: string, slug: string}>} tags - 
 * @param {number} maxCount -  10
 * @returns {Array<{name: string, slug: string}>} 
 */
export const limitTags = (tags, maxCount = 10) => {
  return tags.slice(0, maxCount);
};

/**
 * 
 * @param {...string} texts - 
 * @returns {Array<{name: string, slug: string}>} 
 */
export const extractTagsFromMultiple = (...texts) => {
  const allTags = [];

  texts.forEach(text => {
    const tags = extractTags(text);
    allTags.push(...tags);
  });

  // 
  const uniqueTags = [];
  const seenSlugs = new Set();

  allTags.forEach(tag => {
    if (!seenSlugs.has(tag.slug)) {
      seenSlugs.add(tag.slug);
      uniqueTags.push(tag);
    }
  });

  return limitTags(uniqueTags);
};

