// use-guidelines-actions v1: Hook
//  UploadGuidelines.js 

import { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

/**
 * useGuidelinesActions Hook - 
 * @param {Object} params - 
 * @param {boolean} params.guidelinesConfirmed - 
 * @param {Object} params.user - 
 * @returns {Object} 
 */
const useGuidelinesActions = ({ guidelinesConfirmed, user }) => {
  const navigate = useNavigate();

  // 
  const handleConfirm = useCallback(() => {
    if (guidelinesConfirmed) {
      // 
      localStorage.setItem('tag_guidelines_accepted', 'true');

      // 
      if (user) {
        // 
        navigate('/upload/form');
      } else {
        // 
        navigate('/login');
      }
    }
  }, [guidelinesConfirmed, user, navigate]);

  // 
  const handleBackToHome = useCallback(() => {
    navigate('/');
  }, [navigate]);

  return {
    handleConfirm,
    handleBackToHome,
  };
};

export default useGuidelinesActions;

