/**
 * 
 * @param {Object} item - 
 * @param {string} dataType - 
 * @param {number} index - 
 * @returns {Object} 
 */
export const parseItemInfo = (item, dataType, index) => {
  const defaultInfo = {
    title: ` ${index + 1}`,
    creator: '',
    createdAt: '',
    size: 0,
  };

  if (!item || typeof item !== 'object') {
    return defaultInfo;
  }

  // 
  switch (dataType) {
    case 'collaborations':
      return {
        title:
          item.title ||
          item.name ||
          item.collaborationTitle ||
          ` ${index + 1}`,
        creator:
          item.author?.name ||
          item.author?.id ||
          item.creator ||
          item.owner ||
          item.userId ||
          item.createdBy ||
          '',
        createdAt: item.createdAt || item.createdDate || item.date || '',
        size: JSON.stringify(item).length,
      };

    case 'portfolios':
      return {
        title: item.title || item.name || item.workTitle || ` ${index + 1}`,
        creator:
          item.author?.name ||
          item.artist ||
          item.creator ||
          item.userId ||
          '',
        createdAt: item.createdAt || item.uploadDate || item.date || '',
        size: JSON.stringify(item).length,
      };

    case 'comments':
      return {
        title:
          item.text || item.content
            ? (item.text || item.content).substring(0, 30) + '...'
            : ` ${index + 1}`,
        creator:
          item.authorName ||
          item.author?.name ||
          item.author ||
          item.userId ||
          item.commenter ||
          '',
        createdAt: item.createdAt || item.commentDate || item.date || '',
        size: JSON.stringify(item).length,
      };

    case 'notifications':
      return {
        title:
          item.title || item.message || item.content || ` ${index + 1}`,
        creator:
          item.senderName ||
          item.sender?.name ||
          item.senderId ||
          item.userId ||
          '',
        createdAt: item.createdAt || item.date || '',
        size: JSON.stringify(item).length,
      };

    case 'likes':
      return {
        title:
          item.workTitle ||
          item.title ||
          item.artworkTitle ||
          ` ${index + 1}`,
        creator:
          item.userName ||
          item.user?.name ||
          item.userId ||
          item.likerId ||
          '',
        createdAt: item.createdAt || item.likedAt || item.date || '',
        size: JSON.stringify(item).length,
      };

    case 'views':
      // 
      if (typeof item === 'object' && item.totalViews !== undefined) {
        return {
          title: ` (ID: ${index + 1})`,
          creator: `: ${item.totalViews}`,
          createdAt: `: ${item.userViews?.length || 0}, : ${
            item.visitorViews?.length || 0
          }`,
          size: JSON.stringify(item).length,
        };
      } else {
        return {
          title: ` ${index + 1}`,
          creator: '',
          createdAt: 'N/A',
          size: JSON.stringify(item).length,
        };
      }

    case 'drafts': {
      //  - 
      const draftTitle =
        item.title ||
        item.draftTitle ||
        item.name ||
        item.projectTitle ||
        item.collaborationTitle ||
        ` ${index + 1}`;

      const draftCreator =
        item.userId ||
        item.author?.name ||
        item.author ||
        item.creator ||
        item.owner ||
        '';

      const draftTime = item.createdAt || item.updatedAt || item.date || '';

      return {
        title: draftTitle,
        creator: draftCreator,
        createdAt: draftTime,
        size: JSON.stringify(item).length,
      };
    }

    default:
      return {
        title: item.title || item.name || ` ${index + 1}`,
        creator: item.creator || item.author || '',
        createdAt: item.createdAt || item.date || '',
        size: JSON.stringify(item).length,
      };
  }
};

/**
 * localStorage
 * @param {string} dataType - 
 * @returns {Array} 
 */
export const filterKeysByDataType = dataType => {
  const allKeys = Object.keys(localStorage);
  let filteredKeys = [];

  switch (dataType) {
    case 'collaborations':
      // 
      filteredKeys = allKeys.filter(
        key =>
          key.toLowerCase().includes('collaboration') && !key.includes('draft')
      );
      break;
    case 'portfolios':
      filteredKeys = allKeys.filter(key => key.startsWith('portfolio_'));
      break;
    case 'comments':
      filteredKeys = allKeys.filter(key => key.includes('comment'));
      break;
    case 'notifications':
      filteredKeys = allKeys.filter(key => key.includes('notification'));
      break;
    case 'likes':
      filteredKeys = allKeys.filter(key => key.includes('like'));
      break;
    case 'views':
      filteredKeys = allKeys.filter(key => key.includes('view'));
      break;
    case 'drafts':
      // 
      filteredKeys = allKeys.filter(key => key.includes('draft'));
      break;
    default:
      filteredKeys = allKeys;
  }

  return filteredKeys;
};

/**
 * 
 * @param {Array} filteredKeys - 
 * @param {string} dataType - 
 * @returns {Array} 
 */
export const buildDataList = (filteredKeys, dataType) => {
  return filteredKeys.map(key => {
    const value = localStorage.getItem(key);
    let parsedValue = null;
    let itemCount = 0;
    let preview = '';
    let items = [];

    try {
      parsedValue = JSON.parse(value);
      if (Array.isArray(parsedValue)) {
        itemCount = parsedValue.length;
        preview = ` ${itemCount} `;
        items = parsedValue.map((item, index) => {
          // 
          const itemInfo = parseItemInfo(item, dataType, index);
          return {
            id: item.id || item.collaborationId || `item_${index}`,
            groupKey: key, // groupKey
            ...itemInfo,
            rawData: item,
          };
        });
      } else if (typeof parsedValue === 'object') {
        itemCount = Object.keys(parsedValue).length;
        preview = ` ${itemCount} `;

        // ID
        if (dataType === 'drafts' && key.includes('draft')) {
          const allDrafts = [];
          Object.entries(parsedValue).forEach(([userId, userDrafts]) => {
            if (Array.isArray(userDrafts)) {
              userDrafts.forEach((draft, index) => {
                const itemInfo = parseItemInfo(draft, dataType, index);
                allDrafts.push({
                  id: draft.id || `draft_${userId}_${index}`,
                  groupKey: key, // groupKey
                  ...itemInfo,
                  rawData: draft,
                  userId: userId, // ID
                  // ID
                  title: `${itemInfo.title} (: ${userId})`,
                });
              });
            }
          });
          items = allDrafts;
        } else {
          // 
          items = Object.entries(parsedValue).map(([subKey, item], index) => {
            const itemInfo = parseItemInfo(item, dataType, index);
            return {
              id: subKey || `item_${index}`,
              groupKey: key, // groupKey
              ...itemInfo,
              rawData: item,
            };
          });
        }
      } else {
        // 
        preview = `: ${typeof parsedValue}`;
        items = [
          {
            id: key,
            groupKey: key, // groupKey
            title: key,
            creator: '',
            createdAt: '',
            size: JSON.stringify(parsedValue).length,
            rawData: parsedValue,
          },
        ];
      }
    } catch (error) {
      console.error(':', key, error);
      preview = '';
      items = [
        {
          id: key,
          groupKey: key, // groupKey
          title: key,
          creator: '',
          createdAt: '',
          size: 0,
          rawData: null,
        },
      ];
    }

    return {
      key,
      preview,
      itemCount,
      items,
    };
  });
};
