// UploadArtMarket_refactored v1: 
//  UploadArtMarket.js Hook

import React, { useState } from 'react';

import UploadSuccess from '../upload/UploadSuccess';

import {
  ArtMarketHeader,
  FileUploadSection,
  ArtMarketForm,
  ArtMarketFooter,
} from './components';
import { useArtMarketForm, useArtMarketActions } from './hooks';

/**
 * UploadArtMarket_refactored  - 
 * @returns {JSX.Element} 
 */
const UploadArtMarket_refactored = () => {
  const [showSuccess, setShowSuccess] = useState(false);

  // Hook
  const {
    formData,
    errors,
    isSubmitting,
    handleFormChange,
    handleFileUpload,
    validateForm,
    setSubmitting,
  } = useArtMarketForm();

  // 
  const handleSuccess = () => {
    setShowSuccess(true);
  };

  // Hook
  const actions = useArtMarketActions({
    formData,
    validateForm,
    setSubmitting,
    onSuccess: handleSuccess,
  });

  // UploadSuccess
  if (showSuccess) {
    return <UploadSuccess module='Art Market' />;
  }

  return (
    <div className='bg-white rounded-lg shadow-sm border border-gray-200 p-6'>
      {/* Header Section */}
      <ArtMarketHeader />

      {/* Form */}
      <form className='space-y-6'>
        {/* File Upload Section */}
        <FileUploadSection onFileUpload={handleFileUpload} />

        {/* Form Fields */}
        <ArtMarketForm
          formData={formData}
          onFormChange={handleFormChange}
          errors={errors}
        />

        {/* Footer Section */}
        <ArtMarketFooter
          formData={formData}
          onFormChange={handleFormChange}
          onSubmit={actions.handleSubmit}
          isSubmitting={isSubmitting}
        />
      </form>
    </div>
  );
};

export default UploadArtMarket_refactored;
