// 
export const notificationTypeMap = {
  System: 'system',
  Gallery: 'gallery',
  Collaboration: 'collaboration',
  // Works  All 
  Follow: 'follow',
  Like: 'like',
  Comment: 'comment',
};

// 
export const tabOptions = ['All', 'System', 'Gallery', 'Collaboration'];

// 
export const timeFilterOptions = ['Last 7 days', 'Today', 'Last 30 days'];

// 
export const getDefaultFilters = () => ({
  activeTab: 'All',
  timeFilter: 'Last 7 days',
});

// 
export const getNotificationCategory = notificationType => {
  switch (notificationType) {
    case 'system':
      return 'System';
    case 'gallery':
    case 'comment':
    case 'like':
    case 'follow':
      return 'Gallery';
    case 'collaboration':
    case 'collaboration_application':
    case 'review_request':
    case 'review_response':
      return 'Collaboration';
    default:
      return 'System';
  }
};
