// use-art-market-actions v1: Hook
//  UploadArtMarket.js 

import { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

import { formatFormDataForSubmission } from '../utils/formDataHelpers';

/**
 * useArtMarketActions Hook - 
 * @param {Object} params - 
 * @param {Object} params.formData - 
 * @param {Function} params.validateForm - 
 * @param {Function} params.setSubmitting - 
 * @param {Function} params.onSuccess - 
 * @returns {Object} 
 */
const useArtMarketActions = ({
  formData,
  validateForm,
  setSubmitting,
  onSuccess,
}) => {
  const navigate = useNavigate();

  // 
  const handleSubmit = useCallback(
    async e => {
      e.preventDefault();

      // 
      if (!validateForm()) {
        return;
      }

      setSubmitting(true);

      try {
        // 
        const formattedData = formatFormDataForSubmission(formData);
        console.log('Art Market form submitted:', formattedData);

        // API
        // await submitArtMarketData(formattedData);

        // API
        await new Promise(resolve => setTimeout(resolve, 1000));

        // 
        if (onSuccess) {
          onSuccess();
        }
      } catch (error) {
        console.error('Error submitting form:', error);
        // 
      } finally {
        setSubmitting(false);
      }
    },
    [formData, validateForm, setSubmitting, onSuccess]
  );

  // 
  const handleCancel = useCallback(() => {
    navigate('/upload/form');
  }, [navigate]);

  return {
    handleSubmit,
    handleCancel,
  };
};

export default useArtMarketActions;
