import React from 'react';

/**
 * 
 * @param {Object} props - 
 * @param {Array} props.items - 
 * @param {Array} props.selectedItems - 
 * @param {Function} props.toggleItemSelection - 
 * @param {string} props.viewMode - 
 */
const DataList = ({ items, selectedItems, toggleItemSelection, viewMode }) => {
  if (items.length === 0) {
    return (
      <div className='p-8 text-center text-gray-500'></div>
    );
  }

  if (viewMode === 'table') {
    return (
      <div className='overflow-x-auto'>
        <table className='min-w-full divide-y divide-gray-200'>
          <thead className='bg-gray-50'>
            <tr>
              <th className='px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider'>
                
              </th>
              <th className='px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider'>
                
              </th>
              <th className='px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider'>
                
              </th>
              <th className='px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider'>
                
              </th>
              <th className='px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider'>
                
              </th>
            </tr>
          </thead>
          <tbody className='bg-white divide-y divide-gray-200'>
            {items.map(item => (
              <tr key={item.id} className='hover:bg-gray-50'>
                <td className='px-6 py-4 whitespace-nowrap'>
                  <input
                    type='checkbox'
                    checked={selectedItems.includes(item.id)}
                    onChange={() => toggleItemSelection(item.id)}
                    className='mr-2'
                  />
                </td>
                <td className='px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900'>
                  {item.title}
                </td>
                <td className='px-6 py-4 whitespace-nowrap text-sm text-gray-500'>
                  {item.creator}
                </td>
                <td className='px-6 py-4 whitespace-nowrap text-sm text-gray-500'>
                  {item.createdAt}
                </td>
                <td className='px-6 py-4 whitespace-nowrap text-sm text-gray-500'>
                  {item.size} B
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }

  // 
  return (
    <div className='divide-y divide-gray-200'>
      {items.map(item => (
        <div
          key={item.id}
          className='p-4 hover:bg-gray-50 flex items-center space-x-4'
        >
          <input
            type='checkbox'
            checked={selectedItems.includes(item.id)}
            onChange={() => toggleItemSelection(item.id)}
            className='mr-2'
          />
          <div className='flex-1 min-w-0'>
            <div className='flex items-center justify-between'>
              <div className='flex-1'>
                <h4 className='text-sm font-medium text-gray-900 truncate'>
                  {item.title}
                </h4>
                <div className='flex items-center space-x-4 text-sm text-gray-500'>
                  <span>: {item.creator}</span>
                  <span>: {item.createdAt}</span>
                  <span>: {item.size} B</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default DataList;
