import React from 'react';

/**
 * 
 * @param {Object} props - 
 * @param {string|number} props.count - 
 * @param {string} props.color -  ('gray', 'blue', 'green', 'red', 'yellow', 'purple')
 * @param {string} props.size -  ('sm', 'md', 'lg')
 * @param {string} props.variant -  ('default', 'outline', 'dot')
 * @param {string} props.className - CSS
 */
export function Badge(props) {
  const {
    count,
    color = 'gray',
    size = 'md',
    variant = 'default',
    className = '',
    ...restProps
  } = props;

  // 
  const colorClasses = {
    gray: 'bg-gray-100 text-gray-600 border-gray-200',
    blue: 'bg-blue-100 text-blue-600 border-blue-200',
    green: 'bg-green-100 text-green-600 border-green-200',
    red: 'bg-red-100 text-red-600 border-red-200',
    yellow: 'bg-yellow-100 text-yellow-600 border-yellow-200',
    purple: 'bg-purple-100 text-purple-600 border-purple-200',
  };

  // 
  const sizeClasses = {
    sm: 'text-xs px-1.5 py-0.5',
    md: 'text-xs px-2 py-1',
    lg: 'text-sm px-2.5 py-1.5',
  };

  // 
  const variantClasses = {
    default: 'rounded-full',
    outline: 'rounded-full border',
    dot: 'rounded-full w-2 h-2',
  };

  // dotcountsize
  if (variant === 'dot') {
    return (
      <span
        className={`inline-block ${colorClasses[color]} ${variantClasses[variant]} ${className}`}
        {...restProps}
      />
    );
  }

  // count0null
  if (!count || count === 0) {
    return null;
  }

  const badgeClasses = `${colorClasses[color]} ${sizeClasses[size]} ${variantClasses[variant]} ${className}`;

  return (
    <span
      className={`inline-flex items-center justify-center font-medium ${badgeClasses}`}
      {...restProps}
    >
      {count}
    </span>
  );
}
