// comments-section v1: 

import React, { useMemo } from 'react';

import CommentInput from './CommentInput';
import CommentItem from './CommentItem';

/**
 * 
 * @param {Array} comments - 
 * @param {string} comment - 
 * @param {Function} onCommentChange - 
 * @param {Function} onCommentSubmit - 
 * @param {Function} onCommentUserClick - 
 * @param {Function} onCommentLike - 
 * @param {Function} onCommentReply - 
 * @param {Function} onCommentDelete - 
 * @param {Object} replyingTo - 
 * @param {Object} replyDrafts - 
 * @param {Function} onStartReply - 
 * @param {Function} onCancelReply - 
 * @param {Function} onSetReplyDraft - 
 * @param {Function} onSubmitReply - 
 * @param {string} replyTo - ID
 * @param {Function} onCancelReplyOld - 
 * @param {string} className - CSS
 */
const CommentsSection = ({
  comments,
  comment,
  onCommentChange,
  onCommentSubmit,
  onCommentUserClick,
  onCommentLike,
  onCommentReply,
  onCommentDelete,
  replyingTo,
  replyDrafts,
  onStartReply,
  onCancelReply,
  onSetReplyDraft,
  onSubmitReply,
  replyTo,
  onCancelReplyOld,
  className = '',
}) => {
  // parentId
  const topLevelComments = useMemo(
    () =>
      comments
        .filter(c => !c.parentId)
        .sort((a, b) => a.createdAt - b.createdAt),
    [comments]
  );

  return (
    <div className={`bg-white rounded-lg p-6 ${className}`}>
      <h3 className='text-lg font-semibold text-gray-900 mb-4'>
        Comments ({comments.length})
      </h3>

      {/* Comment Input */}
      <CommentInput
        comment={comment}
        onCommentChange={onCommentChange}
        onCommentSubmit={onCommentSubmit}
        replyTo={replyTo}
        onCancelReply={onCancelReplyOld}
      />

      {/* Comments List -  */}
      <div className='space-y-4'>
        {topLevelComments.map(commentItem => (
          <CommentItem
            key={commentItem.id}
            comment={commentItem}
            comments={comments} // 
            onUserClick={onCommentUserClick}
            onLikeClick={onCommentLike}
            onReplyClick={onCommentReply}
            onDeleteClick={onCommentDelete}
            replyingTo={replyingTo}
            replyDrafts={replyDrafts}
            onStartReply={onStartReply}
            onCancelReply={onCancelReply}
            onSetReplyDraft={onSetReplyDraft}
            onSubmitReply={onSubmitReply}
          />
        ))}
      </div>
    </div>
  );
};

export default CommentsSection;
