import React, { useState, useCallback } from 'react';

import styles from './SaveButton.module.css';

/**
 * Save
 * /
 *
 * @param {Object} props
 * @param {boolean} props.isFavorited - 
 * @param {string} props.itemType -  ('work' | 'collaboration')
 * @param {string} props.itemId - ID
 * @param {Function} props.onToggle - 
 * @param {string} props.className - CSS
 * @param {boolean} props.disabled - 
 * @param {string} props.size -  ('sm' | 'md' | 'lg')
 */
export function SaveButton(props) {
  const {
    isFavorited = false,
    itemType,
    itemId,
    onToggle,
    className = '',
    disabled = false,
    size = 'md',
  } = props;

  const [isOptimistic, setIsOptimistic] = useState(false);
  const [optimisticState, setOptimisticState] = useState(null);

  // 
  const currentState = isOptimistic ? optimisticState : isFavorited;

  // 
  const handleClick = useCallback(async () => {
    if (disabled) return;

    // 
    setIsOptimistic(true);
    setOptimisticState(!currentState);

    try {
      // 
      await onToggle(itemType, itemId, !currentState);

      // 
      setIsOptimistic(false);
      setOptimisticState(null);
    } catch (error) {
      // 
      setIsOptimistic(false);
      setOptimisticState(null);
      console.error('Save toggle failed:', error);
    }
  }, [disabled, currentState, onToggle, itemType, itemId]);

  // 
  const handleKeyDown = useCallback(
    e => {
      if (e.key === 'Enter' || e.key === ' ') {
        e.preventDefault();
        handleClick();
      }
    },
    [handleClick]
  );

  // 
  const buttonClass = `
    ${styles.saveButton}
    ${styles[size]}
    ${
      currentState
        ? itemType === 'work'
          ? styles.favoritedWork
          : styles.favoritedCollab
        : styles.unfavorited
    }
    ${disabled ? styles.disabled : ''}
    ${className}
  `.trim();

  // 
  const iconClass = `
    ${styles.icon}
    ${currentState ? styles.filled : styles.outlined}
  `.trim();

  return (
    <button
      type='button'
      className={buttonClass}
      onClick={handleClick}
      onKeyDown={handleKeyDown}
      disabled={disabled}
      aria-pressed={currentState}
      aria-label={currentState ? 'Remove from favorites' : 'Add to favorites'}
      title={currentState ? 'Remove from favorites' : 'Add to favorites'}
    >
      {/*  */}
      <svg
        className={iconClass}
        fill='none'
        stroke='currentColor'
        viewBox='0 0 24 24'
        aria-hidden='true'
      >
        <path
          strokeLinecap='round'
          strokeLinejoin='round'
          strokeWidth={2}
          d='M5 5a2 2 0 012-2h10a2 2 0 012 2v16l-7-3.5L5 21V5z'
        />
      </svg>

      {/*  */}
      <span className={styles.text}>{currentState ? 'Saved' : 'Save'}</span>
    </button>
  );
}
