// use-register-form v1: Hook
//  RegisterPage.js 

import { useState, useCallback } from 'react';

import {
  getInitialFormData,
  validateFormData,
  clearFieldError,
} from '../utils/formDataHelpers';

/**
 * useRegisterForm Hook - 
 * @returns {Object} 
 */
const useRegisterForm = () => {
  const [formData, setFormData] = useState(getInitialFormData());
  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [isSendingCode, setIsSendingCode] = useState(false);

  // 
  const handleInputChange = useCallback(
    e => {
      const { name, value } = e.target;

      setFormData(prev => ({
        ...prev,
        [name]: value,
      }));

      // 
      if (errors[name]) {
        setErrors(prev => clearFieldError(prev, name));
      }
    },
    [errors]
  );

  // 
  const validateForm = useCallback(() => {
    const validation = validateFormData(formData);
    setErrors(validation.errors);
    return validation.isValid;
  }, [formData]);

  // 
  const resetForm = useCallback(() => {
    setFormData(getInitialFormData());
    setErrors({});
    setIsLoading(false);
    setIsSendingCode(false);
  }, []);

  // 
  const setLoading = useCallback(loading => {
    setIsLoading(loading);
  }, []);

  // 
  const setSendingCode = useCallback(sending => {
    setIsSendingCode(sending);
  }, []);

  // 
  const setError = useCallback((fieldName, errorMessage) => {
    setErrors(prev => ({
      ...prev,
      [fieldName]: errorMessage,
    }));
  }, []);

  // 
  const setGeneralError = useCallback(errorMessage => {
    setErrors(prev => ({
      ...prev,
      general: errorMessage,
    }));
  }, []);

  return {
    formData,
    errors,
    isLoading,
    isSendingCode,
    handleInputChange,
    validateForm,
    resetForm,
    setLoading,
    setSendingCode,
    setError,
    setGeneralError,
  };
};

export default useRegisterForm;
