// RegisterPage_refactored v1: 
//  RegisterPage.js Hook

import React from 'react';

import { RegisterHeader, RegisterForm, RegisterFooter } from './components';
import { useRegisterForm, useRegisterActions } from './hooks';

/**
 * RegisterPage_refactored  - 
 * @returns {JSX.Element} 
 */
const RegisterPage_refactored = () => {
  // Hook
  const {
    formData,
    errors,
    isLoading,
    isSendingCode,
    handleInputChange,
    validateForm,
    setLoading,
    setSendingCode,
    setError,
    setGeneralError,
  } = useRegisterForm();

  // Hook
  const actions = useRegisterActions({
    formData,
    validateForm,
    setLoading,
    setSendingCode,
    setError,
    setGeneralError,
  });

  return (
    <div className='min-h-screen bg-gray-50 flex flex-col'>
      {/* Main Content */}
      <div className='flex-1 flex items-center justify-center px-4 sm:px-6 lg:px-8'>
        <div className='max-w-md w-full space-y-8'>
          {/* Register Card */}
          <div className='bg-white rounded-lg shadow-lg p-8'>
            {/* Header Section */}
            <RegisterHeader />

            {/* Form Section */}
            <RegisterForm
              formData={formData}
              errors={errors}
              onInputChange={handleInputChange}
              onSendCode={actions.sendVerificationCode}
              onSubmit={actions.handleSubmit}
              isLoading={isLoading}
              isSendingCode={isSendingCode}
            />

            {/* Footer Section */}
            <RegisterFooter onLoginClick={actions.handleLoginClick} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default RegisterPage_refactored;
