import React from 'react';

/**
 * 
 * @param {Object} props - 
 * @param {string} props.dataType - 
 * @param {number} props.totalItems - 
 * @param {string} props.searchTerm - 
 * @param {Function} props.setSearchTerm - 
 * @param {string} props.sortBy - 
 * @param {Function} props.setSortBy - 
 * @param {string} props.sortOrder - 
 * @param {Function} props.toggleSortOrder - 
 * @param {string} props.viewMode - 
 * @param {Function} props.toggleViewMode - 
 */
const Toolbar = ({
  dataType,
  totalItems,
  searchTerm,
  setSearchTerm,
  sortBy,
  setSortBy,
  sortOrder,
  toggleSortOrder,
  viewMode,
  toggleViewMode,
}) => {
  // 
  const getDataTypeTitle = () => {
    switch (dataType) {
      case 'collaborations':
        return '';
      case 'portfolios':
        return '';
      case 'comments':
        return '';
      case 'notifications':
        return '';
      case 'likes':
        return '';
      case 'views':
        return '';
      case 'drafts':
        return '';
      default:
        return '';
    }
  };

  return (
    <div className='p-4 border-b border-gray-200'>
      <div className='flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4'>
        <div className='flex items-center space-x-4'>
          <h3 className='text-lg font-semibold text-gray-800'>
            {getDataTypeTitle()}
          </h3>
          <span className='text-sm text-gray-500'> {totalItems} </span>
        </div>

        <div className='flex items-center space-x-2'>
          <input
            type='text'
            placeholder='...'
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
            className='px-3 py-1 border border-gray-300 rounded text-sm'
          />

          <select
            value={sortBy}
            onChange={e => setSortBy(e.target.value)}
            className='px-3 py-1 border border-gray-300 rounded text-sm'
          >
            <option value='title'></option>
            <option value='creator'></option>
            <option value='createdAt'></option>
            <option value='size'></option>
          </select>

          <button
            onClick={toggleSortOrder}
            className='px-3 py-1 border border-gray-300 rounded text-sm hover:bg-gray-50'
          >
            {sortOrder === 'asc' ? '' : ''}
          </button>

          <button
            onClick={toggleViewMode}
            className='px-3 py-1 border border-gray-300 rounded text-sm hover:bg-gray-50'
          >
            {viewMode === 'list' ? ' ' : ' '}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Toolbar;
