// use-register-actions v1: Hook
//  RegisterPage.js 

import { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

import { signUp } from '../../../services/supabase/auth';

/**
 * useRegisterActions Hook - 
 * @param {Object} params - 
 * @param {Object} params.formData - 
 * @param {Function} params.validateForm - 
 * @param {Function} params.setLoading - 
 * @param {Function} params.setSendingCode - 
 * @param {Function} params.setError - 
 * @param {Function} params.setGeneralError - 
 * @returns {Object} 
 */
const useRegisterActions = ({
  formData,
  validateForm,
  setLoading,
  setSendingCode,
  setError,
  setGeneralError,
}) => {
  const navigate = useNavigate();

  // 
  const sendVerificationCode = useCallback(
    async email => {
      if (!email) {
        setError('email', 'Please enter your email address');
        return;
      }

      setSendingCode(true);
      try {
        //  Supabase 
        // Supabase v2 signUp 
        // 
        console.log('Preparing to send verification code to:', email);

        // 
        setTimeout(() => {
          setSendingCode(false);
          alert('Verification code has been sent to your email');
        }, 1000);
      } catch (error) {
        console.error('Failed to send verification code:', error);
        setSendingCode(false);
        setError('email', 'Failed to send verification code, please try again');
      }
    },
    [setSendingCode, setError]
  );

  // 
  const handleSubmit = useCallback(
    async e => {
      e.preventDefault();

      if (!validateForm()) {
        return;
      }

      setLoading(true);
      try {
        //  Supabase 
        const { success, user, error } = await signUp(
          formData.email,
          formData.password,
          {
            // 
            // display_name, avatar_url 
          }
        );

        if (success) {
          console.log('Registration successful:', user);
          setLoading(false);
          alert(
            'Registration successful! Please check your email to verify your account, then go to login page.'
          );
          navigate('/login');
        } else {
          console.error('Registration failed:', error);
          setLoading(false);
          setGeneralError(error || 'Registration failed, please try again');
        }
      } catch (error) {
        console.error('Registration error:', error);
        setLoading(false);
        setGeneralError('Registration failed, please try again');
      }
    },
    [formData, validateForm, setLoading, setGeneralError, navigate]
  );

  // 
  const handleLoginClick = useCallback(() => {
    navigate('/login');
  }, [navigate]);

  return {
    sendVerificationCode,
    handleSubmit,
    handleLoginClick,
  };
};

export default useRegisterActions;
