import React from 'react';

/**
 * 
 * @param {Object} props - 
 * @param {number} props.unreadCount - 
 * @param {string} props.className - CSS
 * @param {string} props.size -  ('sm', 'md', 'lg')
 * @param {string} props.color -  ('red', 'blue', 'green', 'orange')
 */
export function NotificationBadge(props) {
  const { unreadCount = 0, className = '', size = 'md', color = 'red' } = props;

  // 0
  if (unreadCount <= 0) {
    return null;
  }

  // 
  const formatCount = count => {
    if (count > 99) return '99+';
    return count.toString();
  };

  // 
  const sizeClasses = {
    sm: 'w-4 h-4 text-xs',
    md: 'w-5 h-5 text-xs',
    lg: 'w-6 h-6 text-sm',
  };

  // 
  const colorClasses = {
    red: 'bg-red-500 text-white',
    blue: 'bg-blue-500 text-white',
    green: 'bg-green-500 text-white',
    orange: 'bg-orange-500 text-white',
  };

  const badgeClasses = `${sizeClasses[size]} ${colorClasses[color]} rounded-full flex items-center justify-center font-medium ${className}`;

  return (
    <div className={badgeClasses}>
      <span>{formatCount(unreadCount)}</span>
    </div>
  );
}
