import React from 'react';

/**
 * 
 * @param {string} message - 
 * @param {string} className - CSS
 * @param {string} size -  ('sm', 'md', 'lg')
 * @param {string} color -  ('blue', 'gray', 'green', 'red')
 * @param {boolean} fullScreen - 
 * @param {string} variant -  ('default', 'simple')
 */
export function LoadingSpinner(props) {
  const {
    message = 'Loading...',
    className = '',
    size = 'md',
    color = 'blue',
    fullScreen = false,
    variant = 'default',
  } = props;

  // 
  const sizeConfig = {
    sm: 'h-6 w-6',
    md: 'h-12 w-12',
    lg: 'h-16 w-16',
  };

  // 
  const colorConfig = {
    blue: 'border-tag-blue',
    gray: 'border-gray-600',
    green: 'border-green-600',
    red: 'border-red-600',
  };

  // 
  if (variant === 'simple') {
    return (
      <div
        className={`animate-spin rounded-full ${sizeConfig[size]} border-b-2 ${colorConfig[color]} mx-auto ${className}`}
      />
    );
  }

  // 
  const containerClasses = fullScreen
    ? `flex items-center justify-center h-screen ${className}`
    : `flex items-center justify-center ${className}`;

  return (
    <div className={containerClasses}>
      <div className='text-center'>
        <div
          className={`animate-spin rounded-full ${sizeConfig[size]} border-b-2 ${colorConfig[color]} mx-auto mb-4`}
        />
        {message && <p className='text-gray-600'>{message}</p>}
      </div>
    </div>
  );
}
