// tag-renderer v1: 

import React from 'react';

import { extractTags } from './tagParser';

/**
 * 
 * @param {string} text - 
 * @param {Function} onTagClick - 
 * @returns {Array} React 
 */
export const renderWithTags = (text, onTagClick) => {
  if (!text || typeof text !== 'string') {
    return text;
  }

  // 
  const tags = extractTags(text);

  if (tags.length === 0) {
    return text;
  }

  // 
  const parts = [];
  let lastIndex = 0;

  tags.forEach(tag => {
    const tagPattern = `#${tag.name}`;
    const tagIndex = text.indexOf(tagPattern, lastIndex);

    if (tagIndex !== -1) {
      // 
      if (tagIndex > lastIndex) {
        parts.push({
          type: 'text',
          content: text.slice(lastIndex, tagIndex),
        });
      }

      // 
      parts.push({
        type: 'tag',
        content: tagPattern,
        tag: tag,
      });

      lastIndex = tagIndex + tagPattern.length;
    }
  });

  // 
  if (lastIndex < text.length) {
    parts.push({
      type: 'text',
      content: text.slice(lastIndex),
    });
  }

  //  React 
  return parts.map((part, index) => {
    if (part.type === 'tag') {
      return (
        <TagLink key={index} tag={part.tag} onClick={onTagClick}>
          {part.content}
        </TagLink>
      );
    } else {
      return <span key={index}>{part.content}</span>;
    }
  });
};

/**
 * 
 */
const TagLink = ({ tag, onClick, children }) => {
  const handleClick = e => {
    // 
    if (onClick) {
      e.preventDefault();
      onClick(tag);
    }
    // 
  };

  return (
    <a
      href={`/t/${tag.slug}`}
      onClick={handleClick}
      target='_blank'
      rel='noopener noreferrer'
      className='text-blue-600 hover:text-blue-800 underline cursor-pointer'
      title={`View all content tagged with ${tag.name}`}
    >
      {children}
    </a>
  );
};

/**
 * 
 * @param {Array<{name: string, slug: string}>} tags - 
 * @param {Function} onTagClick - 
 * @param {string} className -  CSS 
 * @returns {React.Element} 
 */
export const renderTagList = (tags, onTagClick, className = '') => {
  if (!tags || tags.length === 0) {
    return null;
  }

  return (
    <div className={`flex flex-wrap gap-2 ${className}`}>
      {tags.map((tag, index) => (
        <TagLink key={`${tag.slug}-${index}`} tag={tag} onClick={onTagClick}>
          #{tag.name}
        </TagLink>
      ))}
    </div>
  );
};
