import { useState, useEffect, useCallback } from 'react';

import { favoritesService } from '../../../services';
import { ITEM_TYPES } from '../../../services/favoritesService';
import { getCurrentUser } from '../../../utils/currentUser';

/**
 * Hook
 * @param {string} viewedUserId - ID
 * @returns {Object} 
 */
export const useFavorites = (viewedUserId = null) => {
  // 
  const [favorites, setFavorites] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pagination, setPagination] = useState({
    cursor: null,
    hasMore: false,
    total: 0,
  });

  // 
  const loadFavorites = useCallback(
    async (reset = false) => {
      try {
        setLoading(true);
        setError(null);

        // ID
        let targetUserId = viewedUserId;

        if (!targetUserId) {
          // viewedUserId
          const currentUser = getCurrentUser();
          console.log('[useFavorites] :', currentUser);

          if (!currentUser?.id) {
            console.warn('[useFavorites] ');
            setFavorites([]);
            setPagination({
              cursor: null,
              hasMore: false,
              total: 0,
            });
            return;
          }
          targetUserId = currentUser.id;
        }

        console.log('[useFavorites] ID:', targetUserId);

        const params = {
          userId: targetUserId, // ID
          type: 'all', // all
          cursor: reset ? null : pagination.cursor,
          limit: 12,
        };

        console.log('[useFavorites] :', params);

        const result = await favoritesService.getFavorites(params);

        if (result.success) {
          const newFavorites = result.data.items;
          console.log('[useFavorites] :', newFavorites);
          console.log(
            '[useFavorites] :',
            newFavorites.reduce((acc, fav) => {
              acc[fav.itemType] = (acc[fav.itemType] || 0) + 1;
              return acc;
            }, {})
          );

          if (reset) {
            setFavorites(newFavorites);
          } else {
            setFavorites(prev => [...prev, ...newFavorites]);
          }

          setPagination({
            cursor: result.data.pagination.cursor,
            hasMore: result.data.pagination.hasMore,
            total: result.data.pagination.total,
          });

          console.log('[useFavorites] :', result.data.pagination);
        } else {
          console.error('[useFavorites] :', result.error);
          setError('Failed to load favorites');
        }
      } catch (err) {
        console.error('[useFavorites] :', err);
        setError(err.message || 'Failed to load favorites');
      } finally {
        setLoading(false);
      }
    },
    [] // pagination.cursor
  );

  // 
  useEffect(() => {
    console.log('[useFavorites] ...');
    loadFavorites(true);
  }, [loadFavorites, viewedUserId]);

  // 

  // 
  const handleLoadMore = useCallback(() => {
    if (!loading && pagination.hasMore) {
      console.log('[useFavorites] ...');
      // loadFavoritespagination.cursor
      loadFavorites(false);
    }
  }, [loading, pagination.hasMore, loadFavorites]);

  // 
  const handleRemoveFavorite = useCallback(favoriteId => {
    console.log('[useFavorites] :', favoriteId);
    setFavorites(prev => prev.filter(fav => fav.id !== favoriteId));
    setPagination(prev => ({
      ...prev,
      total: Math.max(0, prev.total - 1),
    }));
  }, []);

  return {
    // 
    favorites,
    loading,
    error,
    pagination,

    // 
    handleLoadMore,
    handleRemoveFavorite,
    refresh: () => loadFavorites(true),
  };
};
