/**
 * 
 * @param {Object} formData - 
 * @param {Array} selectedFiles - 
 * @returns {Object} 
 */
export const validateFormData = (formData, selectedFiles) => {
  const errors = {};

  if (!formData.title.trim()) {
    errors.title = 'Title is required';
  }

  if (formData.tags.length === 0) {
    errors.tags = 'At least one tag is required';
  }

  if (selectedFiles.length === 0) {
    errors.files = 'Please select at least one file';
  }

  return errors;
};

/**
 * 
 * @param {Object} formData - 
 * @param {Array} uploadedFiles - 
 * @returns {Object} 
 */
export const formatFormDataForSubmission = (formData, uploadedFiles) => ({
  title: formData.title.trim(),
  category: 'design', // 
  description: formData.description.trim(),
  tags: [...formData.tags, ...formData.software], // tags 
  imagePaths: uploadedFiles.map(file => file.path), //  key
  thumbnailPath: uploadedFiles[0]?.path || '', //  key
  // imageKeyIndexedDBkey
  imageKey: uploadedFiles[0]?.path || '',
  isPublic: true,
});

/**
 * 
 * @param {Object} errors - 
 * @param {string} fieldName - 
 * @returns {Object} 
 */
export const clearFieldError = (errors, fieldName) => {
  const newErrors = { ...errors };
  if (newErrors[fieldName]) {
    delete newErrors[fieldName];
  }
  return newErrors;
};
