// form-data-helpers v1: 
//  RegisterPage.js 

/**
 * 
 * @returns {Object} 
 */
export const getInitialFormData = () => {
  return {
    email: '',
    verificationCode: '',
    password: '',
    confirmPassword: '',
  };
};

/**
 * 
 * @param {string} email - 
 * @returns {boolean} 
 */
export const validateEmail = email => {
  return /\S+@\S+\.\S+/.test(email);
};

/**
 *  SCAD 
 * @param {string} email - 
 * @returns {boolean}  SCAD 
 */
export const validateScadEmail = email => {
  if (!email) return false;
  const emailLower = email.toLowerCase();
  return emailLower.endsWith('@scad.edu');
};

/**
 * 
 * @param {string} code - 
 * @returns {boolean} 
 */
export const validateVerificationCode = code => {
  return /^\d{6}$/.test(code);
};

/**
 * 
 * @param {string} password - 
 * @returns {boolean} 
 */
export const validatePassword = password => {
  return password.length >= 6;
};

/**
 * 
 * @param {string} password - 
 * @param {string} confirmPassword - 
 * @returns {boolean} 
 */
export const validatePasswordConfirmation = (password, confirmPassword) => {
  return password === confirmPassword;
};

/**
 * 
 * @param {Object} formData - 
 * @returns {Object}  { isValid: boolean, errors: Object }
 */
export const validateFormData = formData => {
  const errors = {};

  // 
  if (!formData.email) {
    errors.email = 'Please enter your email address';
  } else if (!validateEmail(formData.email)) {
    errors.email = 'Please enter a valid email address';
  } else if (!validateScadEmail(formData.email)) {
    errors.email =
      'Only @scad.edu email addresses are allowed for registration';
  }

  // 
  if (!formData.verificationCode) {
    errors.verificationCode = 'Please enter verification code';
  } else if (!validateVerificationCode(formData.verificationCode)) {
    errors.verificationCode = 'Verification code should be 6 digits';
  }

  // 
  if (!formData.password) {
    errors.password = 'Please enter password';
  } else if (!validatePassword(formData.password)) {
    errors.password = 'Password must be at least 6 characters';
  }

  // 
  if (!formData.confirmPassword) {
    errors.confirmPassword = 'Please confirm password';
  } else if (
    !validatePasswordConfirmation(formData.password, formData.confirmPassword)
  ) {
    errors.confirmPassword = 'Passwords do not match';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors,
  };
};

/**
 * 
 * @param {Object} errors - 
 * @param {string} fieldName - 
 * @returns {Object} 
 */
export const clearFieldError = (errors, fieldName) => {
  if (errors[fieldName]) {
    const newErrors = { ...errors };
    delete newErrors[fieldName];
    return newErrors;
  }
  return errors;
};
