import React from 'react';

import ConfirmDialog from '../../common/ConfirmDialog';

import ActionBar from './components/ActionBar';
import DataList from './components/DataList';
import Toolbar from './components/Toolbar';
import { useDataManager } from './hooks/useDataManager';
import { useDataOperations } from './hooks/useDataOperations';
import { useSearchFilter } from './hooks/useSearchFilter';

/**
 * 
 * @param {Object} props - 
 * @param {string} props.dataType - 
 * @param {Function} props.onDeleteSelected - 
 */
const EnhancedDataListManager = ({ dataType, onDeleteSelected }) => {
  // hooks
  const { dataList, loading, error, reloadData } = useDataManager(dataType);
  const {
    searchTerm,
    setSearchTerm,
    sortBy,
    setSortBy,
    sortOrder,
    toggleSortOrder,
    viewMode,
    toggleViewMode,
    allItems,
    sortedItems,
  } = useSearchFilter(dataList);
  const {
    selectedItems,
    showConfirmDialog,
    toggleItemSelection,
    toggleSelectAll,
    handleDeleteSelected,
    confirmDelete,
    cancelDelete,
  } = useDataOperations(sortedItems, onDeleteSelected, reloadData);

  // 
  if (loading) {
    return (
      <div className='bg-white rounded-lg shadow'>
        <div className='p-8 text-center text-gray-500'>...</div>
      </div>
    );
  }

  // 
  if (error) {
    return (
      <div className='bg-white rounded-lg shadow'>
        <div className='p-8 text-center text-red-500'>: {error}</div>
      </div>
    );
  }

  return (
    <div className='bg-white rounded-lg shadow'>
      {/*  */}
      <Toolbar
        dataType={dataType}
        totalItems={allItems.length}
        searchTerm={searchTerm}
        setSearchTerm={setSearchTerm}
        sortBy={sortBy}
        setSortBy={setSortBy}
        sortOrder={sortOrder}
        toggleSortOrder={toggleSortOrder}
        viewMode={viewMode}
        toggleViewMode={toggleViewMode}
      />

      {/*  */}
      <ActionBar
        selectedItems={selectedItems}
        sortedItems={sortedItems}
        toggleSelectAll={toggleSelectAll}
        handleDeleteSelected={handleDeleteSelected}
      />

      {/*  */}
      <div className='max-h-96 overflow-y-auto'>
        <DataList
          items={sortedItems}
          selectedItems={selectedItems}
          toggleItemSelection={toggleItemSelection}
          viewMode={viewMode}
        />
      </div>

      {/*  */}
      <ConfirmDialog
        isOpen={showConfirmDialog}
        onClose={cancelDelete}
        onConfirm={confirmDelete}
        title=''
        message={` ${selectedItems.length} \n\n`}
        confirmText=''
        cancelText=''
        type='danger'
      />
    </div>
  );
};

export default EnhancedDataListManager;
