import React from 'react';

/**
 * 
 * @param {Object} props - 
 * @param {number} props.progress -  (0-100)
 * @param {string} props.color -  ('blue', 'green', 'purple', 'red', 'orange')
 * @param {string} props.size -  ('sm', 'md', 'lg')
 * @param {string} props.className - CSS
 * @param {boolean} props.showLabel - 
 * @param {string} props.label - 
 */
export function ProgressBar(props) {
  const {
    progress = 0,
    color = 'blue',
    size = 'md',
    className = '',
    showLabel = false,
    label,
    ...restProps
  } = props;

  // 0-100
  const clampedProgress = Math.max(0, Math.min(100, progress));

  // 
  const colorClasses = {
    blue: 'bg-blue-600',
    green: 'bg-green-600',
    purple: 'bg-purple-600',
    red: 'bg-red-600',
    orange: 'bg-orange-600',
  };

  // 
  const sizeClasses = {
    sm: 'h-1',
    md: 'h-2',
    lg: 'h-3',
  };

  const progressBarClasses = `w-full bg-gray-200 rounded-full ${sizeClasses[size]} ${className}`;
  const progressFillClasses = `${colorClasses[color]} ${sizeClasses[size]} rounded-full transition-all duration-500`;

  return (
    <div className='w-full'>
      {showLabel && (
        <div className='flex justify-between items-center mb-2'>
          <span className='text-sm text-gray-600'>{label || 'Progress'}</span>
          <span className='text-sm font-medium text-gray-900'>
            {clampedProgress}%
          </span>
        </div>
      )}
      <div
        className={progressBarClasses}
        role='progressbar'
        aria-valuemin='0'
        aria-valuemax='100'
        aria-valuenow={clampedProgress}
        aria-label={`Progress: ${clampedProgress}%`}
        {...restProps}
      >
        <div
          className={progressFillClasses}
          style={{ width: `${clampedProgress}%` }}
        />
      </div>
    </div>
  );
}
