/**
 * 
 * 
 */

// 
export const ERROR_TYPES = {
  NETWORK: 'NETWORK',
  VALIDATION: 'VALIDATION',
  AUTHENTICATION: 'AUTHENTICATION',
  AUTHORIZATION: 'AUTHORIZATION',
  NOT_FOUND: 'NOT_FOUND',
  SERVER: 'SERVER',
  CLIENT: 'CLIENT',
  UNKNOWN: 'UNKNOWN',
};

// 
export const ERROR_SEVERITY = {
  LOW: 'LOW',
  MEDIUM: 'MEDIUM',
  HIGH: 'HIGH',
  CRITICAL: 'CRITICAL',
};

// 
const ERROR_MESSAGES = {
  [ERROR_TYPES.NETWORK]: {
    title: 'Network Error',
    message:
      'Unable to connect to the server. Please check your internet connection and try again.',
    userMessage: 'Connection failed. Please try again.',
  },
  [ERROR_TYPES.VALIDATION]: {
    title: 'Validation Error',
    message: 'The provided data is invalid.',
    userMessage: 'Please check your input and try again.',
  },
  [ERROR_TYPES.AUTHENTICATION]: {
    title: 'Authentication Error',
    message: 'You are not authenticated. Please log in.',
    userMessage: 'Please log in to continue.',
  },
  [ERROR_TYPES.AUTHORIZATION]: {
    title: 'Authorization Error',
    message: 'You do not have permission to perform this action.',
    userMessage: 'You do not have permission for this action.',
  },
  [ERROR_TYPES.NOT_FOUND]: {
    title: 'Not Found',
    message: 'The requested resource was not found.',
    userMessage: 'The requested item was not found.',
  },
  [ERROR_TYPES.SERVER]: {
    title: 'Server Error',
    message: 'An internal server error occurred.',
    userMessage: 'Something went wrong. Please try again later.',
  },
  [ERROR_TYPES.CLIENT]: {
    title: 'Client Error',
    message: 'An error occurred in the application.',
    userMessage: 'Something went wrong. Please refresh the page.',
  },
  [ERROR_TYPES.UNKNOWN]: {
    title: 'Unknown Error',
    message: 'An unexpected error occurred.',
    userMessage: 'Something went wrong. Please try again.',
  },
};

/**
 * 
 */
class ErrorHandler {
  constructor() {
    this.errorLog = [];
    this.maxLogSize = 100;
  }

  /**
   * 
   * @param {Error|Object} error - 
   * @returns {string} 
   */
  analyzeErrorType(error) {
    if (!error) return ERROR_TYPES.UNKNOWN;

    // 
    if (
      error.name === 'NetworkError' ||
      error.message?.includes('network') ||
      error.message?.includes('fetch') ||
      error.message?.includes('connection')
    ) {
      return ERROR_TYPES.NETWORK;
    }

    // 
    if (
      error.name === 'ValidationError' ||
      error.message?.includes('validation') ||
      error.message?.includes('invalid')
    ) {
      return ERROR_TYPES.VALIDATION;
    }

    // 
    if (
      error.message?.includes('unauthorized') ||
      error.message?.includes('authentication') ||
      error.status === 401
    ) {
      return ERROR_TYPES.AUTHENTICATION;
    }

    // 
    if (
      error.message?.includes('forbidden') ||
      error.message?.includes('permission') ||
      error.status === 403
    ) {
      return ERROR_TYPES.AUTHORIZATION;
    }

    // 
    if (error.message?.includes('not found') || error.status === 404) {
      return ERROR_TYPES.NOT_FOUND;
    }

    // 
    if (error.status >= 500) {
      return ERROR_TYPES.SERVER;
    }

    // 
    if (error.status >= 400 && error.status < 500) {
      return ERROR_TYPES.CLIENT;
    }

    return ERROR_TYPES.UNKNOWN;
  }

  /**
   * 
   * @param {string} errorType - 
   * @param {Error|Object} error - 
   * @returns {string} 
   */
  determineSeverity(errorType, error) {
    switch (errorType) {
      case ERROR_TYPES.CRITICAL:
      case ERROR_TYPES.SERVER:
        return ERROR_SEVERITY.CRITICAL;
      case ERROR_TYPES.AUTHENTICATION:
      case ERROR_TYPES.AUTHORIZATION:
        return ERROR_SEVERITY.HIGH;
      case ERROR_TYPES.NETWORK:
      case ERROR_TYPES.VALIDATION:
        return ERROR_SEVERITY.MEDIUM;
      default:
        return ERROR_SEVERITY.LOW;
    }
  }

  /**
   * 
   * @param {Error|Object} error - 
   * @param {string} context - 
   * @param {Object} additionalData - 
   */
  logError(error, context = '', additionalData = {}) {
    const errorType = this.analyzeErrorType(error);
    const severity = this.determineSeverity(errorType, error);
    const timestamp = new Date().toISOString();

    const errorEntry = {
      timestamp,
      type: errorType,
      severity,
      context,
      message: error.message || 'Unknown error',
      stack: error.stack,
      additionalData,
    };

    // 
    this.errorLog.push(errorEntry);
    if (this.errorLog.length > this.maxLogSize) {
      this.errorLog.shift();
    }

    // 
    const logMethod = severity === ERROR_SEVERITY.CRITICAL ? 'error' : 'warn';
    console[logMethod](
      `[${errorType}] ${context}:`,
      error.message,
      additionalData
    );

    // 
    if (severity === ERROR_SEVERITY.CRITICAL) {
      console.error('Full error stack:', error.stack);
    }
  }

  /**
   * 
   * @param {Error|Object} error - 
   * @param {string} customMessage - 
   * @returns {Object} 
   */
  getUserMessage(error, customMessage = null) {
    const errorType = this.analyzeErrorType(error);
    const template = ERROR_MESSAGES[errorType];

    return {
      title: template.title,
      message: customMessage || template.message,
      userMessage: template.userMessage,
      type: errorType,
    };
  }

  /**
   * 
   * @param {Error|Object} error - 
   * @param {string} context - 
   * @param {Object} options - 
   * @returns {Object} 
   */
  handleError(error, context = '', options = {}) {
    const {
      log = true,
      customMessage = null,
      additionalData = {},
      rethrow = false,
    } = options;

    // 
    if (log) {
      this.logError(error, context, additionalData);
    }

    // 
    const userMessage = this.getUserMessage(error, customMessage);

    // 
    const standardizedError = {
      success: false,
      error: userMessage.userMessage,
      errorType: userMessage.type,
      errorTitle: userMessage.title,
      errorDetails: userMessage.message,
      context,
      timestamp: new Date().toISOString(),
    };

    // 
    if (rethrow) {
      throw standardizedError;
    }

    return standardizedError;
  }

  /**
   * 
   * @returns {Array} 
   */
  getErrorLog() {
    return [...this.errorLog];
  }

  /**
   * 
   */
  clearErrorLog() {
    this.errorLog = [];
  }

  /**
   * 
   * @param {Error|Object} error - 
   * @returns {boolean} 
   */
  isRecoverable(error) {
    const errorType = this.analyzeErrorType(error);
    return [
      ERROR_TYPES.NETWORK,
      ERROR_TYPES.VALIDATION,
      ERROR_TYPES.AUTHENTICATION,
    ].includes(errorType);
  }
}

// 
export const errorHandler = new ErrorHandler();

/**
 * 
 * @param {Function} asyncFunction - 
 * @param {string} context - 
 * @param {Object} options - 
 * @returns {Function} 
 */
export const withErrorHandling = (
  asyncFunction,
  context = '',
  options = {}
) => {
  return async (...args) => {
    try {
      return await asyncFunction(...args);
    } catch (error) {
      return errorHandler.handleError(error, context, options);
    }
  };
};

export default errorHandler;
