import { useState } from 'react';

import { adminService } from '../../../services/adminService';

/**
 * Hook
 * @param {Function} refreshDataOverview - 
 * @returns {Object} 
 */
export const useAdminOperations = refreshDataOverview => {
  const [operationLog, setOperationLog] = useState([]);
  const [isOperating, setIsOperating] = useState(false);
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [confirmAction, setConfirmAction] = useState(null);
  const [currentDataType, setCurrentDataType] = useState(null);
  const [showDataManager, setShowDataManager] = useState(false);

  // 
  const addLog = (message, type = 'info') => {
    const timestamp = new Date().toLocaleTimeString();
    const logEntry = { timestamp, message, type };
    setOperationLog(prev => [logEntry, ...prev.slice(0, 49)]); // 50
  };

  // 
  const executeClearOperation = async (operation, operationName) => {
    if (isOperating) return;

    setIsOperating(true);
    addLog(`: ${operationName}`, 'info');

    try {
      const result = await operation();

      if (result.success) {
        addLog(` ${result.message}`, 'success');
        // 
        refreshDataOverview();
      } else {
        addLog(` : ${result.error}`, 'error');
      }
    } catch (error) {
      addLog(` : ${error.message}`, 'error');
    } finally {
      setIsOperating(false);
    }
  };

  // 
  const showConfirm = (action, operationName, message) => {
    setConfirmAction(() => () => executeClearOperation(action, operationName));
    setShowConfirmDialog(true);
  };

  // 
  const handleConfirm = () => {
    if (confirmAction) {
      confirmAction();
    }
  };

  // 
  const openDataManager = dataType => {
    setCurrentDataType(dataType);
    setShowDataManager(true);
  };

  // 
  const handleDataManagerDelete = deletedCount => {
    addLog(`  ${deletedCount} `, 'success');
    refreshDataOverview();
  };

  return {
    operationLog,
    isOperating,
    showConfirmDialog,
    currentDataType,
    showDataManager,
    addLog,
    showConfirm,
    handleConfirm,
    openDataManager,
    handleDataManagerDelete,
    setShowConfirmDialog,
    setShowDataManager,
  };
};
