import { useState, useMemo } from 'react';

/**
 * Hook
 * @param {Array} dataList - 
 * @returns {Object} 
 */
export const useSearchFilter = dataList => {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('title');
  const [sortOrder, setSortOrder] = useState('asc');
  const [viewMode, setViewMode] = useState('list'); // 'list' or 'table'

  // 
  const allItems = useMemo(() => {
    return dataList.flatMap(group => group.items || []);
  }, [dataList]);

  // 
  const sortedItems = useMemo(() => {
    let filtered = allItems;

    // 
    if (searchTerm.trim()) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(
        item =>
          item.title?.toLowerCase().includes(term) ||
          item.creator?.toLowerCase().includes(term) ||
          item.createdAt?.toLowerCase().includes(term)
      );
    }

    // 
    filtered.sort((a, b) => {
      let aValue = a[sortBy];
      let bValue = b[sortBy];

      // 
      if (sortBy === 'size') {
        aValue = parseInt(aValue) || 0;
        bValue = parseInt(bValue) || 0;
      } else {
        // 
        aValue = String(aValue || '').toLowerCase();
        bValue = String(bValue || '').toLowerCase();
      }

      if (sortOrder === 'asc') {
        return aValue > bValue ? 1 : -1;
      } else {
        return aValue < bValue ? 1 : -1;
      }
    });

    return filtered;
  }, [allItems, searchTerm, sortBy, sortOrder]);

  // 
  const toggleSortOrder = () => {
    setSortOrder(prev => (prev === 'asc' ? 'desc' : 'asc'));
  };

  // 
  const toggleViewMode = () => {
    setViewMode(prev => (prev === 'list' ? 'table' : 'list'));
  };

  return {
    searchTerm,
    setSearchTerm,
    sortBy,
    setSortBy,
    sortOrder,
    setSortOrder,
    viewMode,
    setViewMode,
    allItems,
    sortedItems,
    toggleSortOrder,
    toggleViewMode,
  };
};
